import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ProfilePageComponent } from './profile-page/profile-page.component';
import { RoutePrivilegeGuard } from 'src/app/erp-procurement/data/services/_AccessControlAuthGuard.service';
import { AllDocumentsComponent } from './employee-documents/all-documents/all-documents.component';

const routes: Routes = [

  {
    path: "my-profile",
    component: ProfilePageComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "EmployeePortalModule",
      requiredPrivilege: ["Dashboard"],
    },
  },

  {
    path: "all-employee-documents",
    component: AllDocumentsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "EmployeePortalModule",
      requiredPrivilege: ["Dashboard"],
    },
  },

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EmployeeProfileRoutingModule { }
