import { SelectionModel } from "@angular/cdk/collections";
import { HttpParams } from "@angular/common/http";
import { Component, OnInit, ViewChild } from "@angular/core";
import { FormGroup, FormBuilder } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { Subject, takeUntil, Subscription } from "rxjs";
import { NotificationServiceService } from "src/@core/Models/Notification/notification-service.service";
import { TokenCookieService } from "src/app/core/service/token-storage-cookies.service";
import { EmployeeDocumentsService } from "src/app/erp-hr/data/employee-services/employee-documents.service";
import { EmployeeService } from "src/app/erp-hr/data/employee-services/employee-management.service";
import { SnackbarService } from "src/app/shared/services/snackbar.service";
import Swal from "sweetalert2";

@Component({
  selector: "app-all-documents",
  templateUrl: "./all-documents.component.html",
  styleUrls: ["./all-documents.component.sass"],
})
export class AllDocumentsComponent implements OnInit {
  displayedColumns: string[] = [
    // "select",
    "index",
    "filename",
    "filetype",
    "action",
  ];

  loading: boolean = false;
  data: any;
  error: any;
  dialogConfig: any;
  destroy$: Subject<boolean> = new Subject<boolean>();

  dataSource!: MatTableDataSource<any>;
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  selection = new SelectionModel<any>(true, []);

  downloadLoading: boolean = false;
  posting: boolean = false;
  showForm: boolean = false;

  Form: FormGroup;
  currentEmail: any;
  currentUser: any;
  constructor(
    private dialog: MatDialog,
    private notificationAPI: NotificationServiceService,
    private employeeDocumentsService: EmployeeDocumentsService,
    private snackbar: SnackbarService,
    private fb: FormBuilder,
    private tokenStorageService: TokenCookieService,
    private employeeService: EmployeeService,

  ) { }

  ngOnInit(): void {
    this.currentEmail = this.tokenStorageService.getUser().email;
    this.currentUser = this.tokenStorageService.getUser();
    this.Form = this.fb.group({
      status: ['ALL']
    });
    this.Form.valueChanges.subscribe(() => {
      this.getData();
    });

    this.getData();
    this.getDataByEmpfCode();
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }
  profileData: any
  getDataByEmpfCode() {
    let param = new HttpParams().set("empNo", this.currentUser.empNo);
    this.employeeService
      .readByEmployeePf(param)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("RES: ", res);
          if (res.entity) {
            this.profileData = res.entity;
            console.log("this.formData: ", this.profileData);

          } else {
            this.snackbar.showNotification("snackbar-danger", 'UPDATE PROFILE BEFORE DOCUMENTS UPLOAD');
          }
        },
        error: (err) => {
          // this.snackbar.showNotification("snackbar-danger", err.message);
        },
        complete: () => { },
      });
  }
  //****************************************************************************************** */
  reqStatuses: any[] = [
    { name: "ALL" },
    { name: "PENDING" },
    { name: "APPROVED" },
    { name: "RETURNED" },
    { name: "REJECTED" },
  ];

  selectedStatus: string = "ALL";
  getSelectSelectedStatus() {
    this.getData();
  }
  getSelectedStatus(status: any) {
    this.Form.patchValue({
      status: status,
    });

    this.getData();
  }


  getData() {
    this.loading = true;
    this.dataSource = new MatTableDataSource([]);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;

    let param = new HttpParams().set("employeeId", this.currentUser.id).set("status", this.Form.value.status);

    this.employeeDocumentsService
      .readByStatusAndEmpId(param)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("Documents displayed on table", res);
          if (res.statusCode === 302) {
            this.data = res.entity;

            this.loading = false;
            this.dataSource = new MatTableDataSource(this.data);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
          } else {
            this.loading = false;

            this.notificationAPI.alertWarning(res.message);
          }
        },
        error: (err) => {
          this.loading = false;
          this.notificationAPI.alertWarning("Server Error: " + err.message);
        },
        complete: () => { },
      });
  }
  refresh() {
    this.getData();
  }

  // "employeeId": 0,
  // "employeeNationalId": "string",
  // "documentType": "string",
  // "attachment": "string",
  // "allowEdit": "string"
  documentTypes: string[] = [
    "Resume/CV",
    "ID/Passport",
    "Proof of address",
    "Educational certificates/degrees",
    "Professional certifications/licenses",
    "Employment history/letters of recommendation",
    "Tax documents",
    "Bank account details",
    "Emergency contact information",
    "Health insurance documents",
    "Other"
];
mngRecord() {
  Swal.fire({
    title: "Upload File",
    html: `
      <select id="documentTypeSelect" class="swal2-select">
        ${this.documentTypes.map(type => `<option>${type}</option>`).join('')}
      </select>
      <input type="file" id="fileInput" class="swal2-input">
    `,
    showCancelButton: true,
    confirmButtonText: "Upload",
    preConfirm: () => {
      const fileInput: HTMLInputElement | null = document.getElementById(
        "fileInput"
      ) as HTMLInputElement;
      const selectedDocumentType = (document.getElementById(
        "documentTypeSelect"
      ) as HTMLSelectElement).value;
      if (fileInput && fileInput.files && fileInput.files[0]) {
        const file = fileInput.files[0];
        const reader = new FileReader();
        reader.onload = (e) => {
          const fileData = (e.target as FileReader).result;
          const finalBody = {
            attachment: fileData,
            documentType: selectedDocumentType,
            employeeId: this.currentUser.id,
            employeeNationalId: this.profileData.nationalId
          };
          console.log(finalBody);

          this.employeeDocumentsService
            .create(finalBody)
            .pipe(takeUntil(this.destroy$))
            .subscribe({
              next: (res) => {
                console.log("res: ", res);

                if (res.statusCode == 201) {
                  this.snackbar.showNotification(
                    "snackbar-success",
                    res.message
                  );
                } else {
                  this.snackbar.showNotification(
                    "snackbar-danger",
                    res.message
                  );
                }
              },
              error: (err) => {
                this.snackbar.showNotification(
                  "snackbar-danger",
                  err.message
                );
                this.posting = false;
              },
              complete: () => {
                this.posting = false;
              },
            }),
            Subscription;
        };
        reader.readAsDataURL(file);
      } else {
        Swal.showValidationMessage("Please select a file");
      }
    },
  });
}

  deleteRecord(id: number) {
    let params = { id: id }
    Swal.fire({
      title: "Are you sure?",
      text: "THIS RECORD WILL BE DELETED PERMANENTLY!!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        this.loading = true;
        this.employeeDocumentsService
          .delete(params)
          .pipe(takeUntil(this.destroy$))
          .subscribe({
            next: (res) => {
              if (res.statusCode == 200) {
                this.getData();
                this.loading = false;
                this.notificationAPI.alertSuccess(
                  "RECORD DELETED SUCCESSFULLY"
                );
              } else {
                this.getData();
                this.loading = false;
                this.notificationAPI.alertWarning(res.message);
              }
            },
            error: (err) => {
              this.loading = false;
              this.notificationAPI.alertSuccess("Server Error: " + err.message);
            },
            complete: () => { },
          });
      }
    });
  }

  input: any;
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.input = filterValue;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  // ****************************************************************************************************************************

  getDocById(id) {
    let param = new HttpParams().set("id", id);
    this.employeeDocumentsService.readById(param).subscribe({
      next: (res) => {
        console.log("res:: ", res);

        this.data = res.entity;
        this.downloadDocument(this.data);
      },

      error: (err) => {
        this.notificationAPI.alertWarning(err.message);
      },
      complete: () => { },
    });
  }

  downloadDocument(row: any) {
    let file = row.file;
    let filename = row.filename;
    let filetype = row.filetype;

    // Download PDF in Chrome etc.
    const source = `${file}`;
    const link = document.createElement("a");
    link.href = source;
    link.download = `${filename}.${filetype}`;
    link.click();
  }
}
